.main-header {
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: $tablet-width) {
    border-bottom: 0;
  }
}

.main-header__wrapper {
  padding-top: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  position: relative;
  align-items: center;

  @media (min-width: $desktop-width) {
    padding-top: 53px;
  }
}

.main-header__logo-link {
  img {
    &:hover {
      opacity: 0.6;
      transition: 0.25s ease-in-out;
    }

    &:active {
      opacity: 0.3;
      transition: 0.25s ease-in-out;
    }
  }
}

.main-header__logo-link--off-state {
  img {
    &:hover {
      opacity: 1;
    }

    &:active {
      opacity: 1;
    }
  }
}
